<template>
    <div style="padding-top: 2px;">
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="代码表名称">
                <el-input v-model="searchFormData.code" placeholder="代码表名称"></el-input>
            </el-form-item>
            <el-form-item label="代码表中文名称">
                <el-input v-model="searchFormData.codeName" placeholder="代码表中文名称"></el-input>
            </el-form-item>
            <el-form-item>
                <el-switch @change=setShowType(searchFormData.onlyShowCodeType)
                           v-model="searchFormData.onlyShowCodeType"
                           active-text="仅显示代码类型"
                           inactive-text="全部显示"
                           size="mini">
                </el-switch>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[14, 20, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>
                </div>
            </div>
        </el-form>
        <hr>

        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)" >
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column fixed prop="isCodeType" label="类别" width="120" :formatter="formatCodeTypeBoolean"></el-table-column>
            <el-table-column fixed prop="code" label="代码表名称" width="180"></el-table-column>
            <el-table-column prop="codeName" label="代码表中文名称" width="180"></el-table-column>
            <el-table-column prop="codeId" label="代码ID" width="180"></el-table-column>
            <el-table-column prop="codeValue" label="代码值" width="180"></el-table-column>
            <el-table-column prop="fatherCode" label="父级代码" width="180"></el-table-column>
            <el-table-column prop="createTime" label="创建时间"></el-table-column>
            <el-table-column prop="updateTime" label="修改时间"></el-table-column>
            <el-table-column prop="remark" label="备注"></el-table-column>
            <el-table-column fixed="right" label="操作" width="120">
                <template slot-scope="scope">
                    <el-row :gutter="10">
                        <el-col :span="8">
                            <el-tooltip v-if="scope.row.isCodeType" class="item" effect="dark" content="修改" placement="left">
                                <el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-xiugai"></i></el-button>
                            </el-tooltip>
                            <el-col v-else :span="8" style="visibility:hidden;">
                                <el-button></el-button>
                            </el-col>
                        </el-col>
                        <el-col :span="8">
                            <el-tooltip class="item" effect="dark" content="删除当前代码表" placement="left">
                                <el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-shanchu icon-red"></i></el-button>
                            </el-tooltip>
                        </el-col>
                    </el-row>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'codeManageList',
        data() {
            return {
                module_name: 'codeManage',
                refresh_page_method: 'refresh_' + this.module_name,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 1,
                    onlyShowCodeType: true,
                    code: '',
                    codeName: '',
                    order: '+code'
                },
                tableData: []
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        async mounted() {
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            formatCodeTypeBoolean(row, column, cellValue) {
                return row.isCodeType === true ? '代码类型' : '代码值';
            },
            setShowType() //设置显示模式
            {
                this.search();
            },
            async r_search_list(searchData) {
                let R = new this.$http();
                let {data} = await R.post(searchData, 'OAMP_search_code_info_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data
            },
            async r_delete_code_type_info(rowData) // 删除代码表信息【代码类型和代码值都可以删除】
            {
                let R = new this.$http();
                let {data} = await R.post(rowData, 'OAMP_delete_code_type_info');
                return data != null && data !== 'null';
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            search() {
                let searchData = this.searchFormData;
                this.r_search_list(searchData)
            },
            onDelete(index, rows)  // 删除
            {
                let rowData = rows[index];
                if (this.r_delete_code_type_info(rowData)) {
                    rows.splice(index, 1);
                    this.$message.success('删除数据成功');
                    this.search();
                }
                else {
                    this.$message.error('删除数据失败');
                }
            },
            on_project_configure(index, rows)  // 项目配置
            {
                // console.log(rows[index])
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



