<template>
    <el-dialog title="代码表信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,['pageDataRef','codeDataRef'])"
    >


        <!--@close="onClose(module_name,'pageDataRef')"-->
        <el-form :model="pageData" :rules="pageDataRules" :inline="true" ref="pageDataRef" label-width="160px" size="mini">
            <el-form-item label="代码表名称" prop="code">
                <el-input v-model="pageData.code" :disabled="isDisabled"></el-input>
            </el-form-item>
            <el-form-item label="代码表中文名称" prop="codeName">
                <el-input v-model="pageData.codeName" :disabled="isDisabled"></el-input>
            </el-form-item>
            <el-form-item label="备注" prop="remark">
                <el-input type="textarea" v-model="pageData.remark" style="width:696px" :disabled="isDisabled"></el-input>
            </el-form-item>
            <div class="v-center">
                <el-button type="primary" @click="reset" size="mini" :disabled="isDisabled">重 置</el-button>
                <el-button v-if="pageInfo.pageModel=='add'" size="mini" type="primary" @click="addCodeTypeInfo" :disabled="isDisabled">新增代码表类型</el-button>
                <el-button v-else type="primary" @click="updateCodeTypeInfo" size="small" :disabled="isDisabled">更新代码表类型</el-button>
            </div>
        </el-form>
        <hr>
        <el-form :model="codeData" :rules="codeDataRules" :inline="true" ref="codeDataRef" label-width="160px" size="mini">
            <el-form-item label="代码" label-width="160px" prop="codeId">
                <el-input v-model="codeData.codeId" style="width: 150px"></el-input>
            </el-form-item>
            <el-form-item label="代码值" label-width="80px" prop="codeValue">
                <el-input v-model="codeData.codeValue" style="width: 150px"></el-input>
            </el-form-item>
            <el-form-item label="父级代码" label-width="80px" prop="fatherCode">
                <el-input v-model="codeData.fatherCode" style="width: 150px"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="addCodeInfoData">新增</el-button>
            </el-form-item>
        </el-form>

        <el-table :data="codeInfoDataList" prop="codeInfoDataList" style="width: 100%" max-height="250" size="mini">
            <el-table-column fixed label="操作" width="120">
                <template slot-scope="scope">
                    <el-button @click.native.prevent="deleteCodeInfoData(scope.$index,codeInfoDataList)" type="text" size="small">移除</el-button>
                </template>
            </el-table-column>
            <el-table-column type="index" label="序号" width="50"></el-table-column>
            <el-table-column prop="codeId" label="代码"></el-table-column>
            <el-table-column prop="codeValue" label="代码值"></el-table-column>
            <el-table-column prop="fatherCode" label="父级代码"></el-table-column>
        </el-table>

        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,['pageDataRef','codeDataRef'])" size="small">关 闭</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        name: 'codeManageInfo',
        data() {
            let checkCodeId = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('请输入代码'));
                }
                setTimeout(() => {
                        if (this.codeInfoDataList)
                            for (let ele of this.codeInfoDataList) {
                                if (ele.codeId === value) {
                                    callback(new Error('当前代码已经存在'))
                                    break;
                                }
                            }
                        callback()
                    }, 200
                );
                return true
            };
            return {
                module_name: 'codeManage',
                refresh_page_method: 'refresh_' + this.module_name,
                isDisabled: false,
                pageData: {
                    code: '',
                    codeName: '',
                    remark: '',
                },
                codeData: {
                    codeId: '',
                    codeValue: '',
                    fatherCode: '',
                },
                codeInfoDataList: [],
                pageDataRules: {
                    code: [{required: true, message: '请输入代码名称', trigger: 'blur'}],
                    codeName: [{required: true, message: '请输入代码中文名称', trigger: 'blur'}],
                },
                codeDataRules: {
                    codeId: [{required: true, validator: checkCodeId, trigger: 'blur'}],
                    codeValue: [{required: true, message: '请输入code值', trigger: 'blur'}],
                }
            }
        },
        computed: {
            pageInfo() {
                this.codeInfoDataList = [];
                this.isDisabled = false;
                let page_data = this.$setPageInfo(this.module_name);
                this.setPageInit(page_data);
                return page_data;
            }
        },
        methods: {
            setPageInit(page_data) /*设置初始化页面数据*/ {
                if (page_data.pageModel !== 'add' && page_data.pageModel !== 'default') {
                    let code = page_data.pageData.code;
                    let searchData = {
                        code: code
                    };
                    this.r_search_code_data_list(searchData);
                }
            },
            async r_search_code_data_list(searchData) {

                let R = new this.$http();
                let {data} = await R.post(searchData, 'OAMP_search_code_data_list');
                this.codeInfoDataList = data.data
            },
            async r_insert_code_type_info(item) /*新增代码表类型信息*/ {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_insert_code_type_info');
                if (data != null && data !== 'null')
                    return data.data;
            },
            async r_insert_code_data(item) /*新增代码表类型信息*/ {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_insert_code_data');
                if (data != null && data !== 'null')
                    return data.data;
            },
            async r_update_code_type_info(item) /*更新代码表类型信息*/ {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_update_code_type_info');
                if (data != null && data !== 'null')
                    return data;
            },
            async r_delete_code_data(item)/*删除代码表值*/ {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_delete_code_data');
                return data !== null && data !== 'null'
            },
            addCodeTypeInfo() /*新增代码表类型信息*/ {
                let _this = this;
                this.$refs['pageDataRef'].validate(async (valid) => {
                    if (valid) {
                        let data = _this.pageData;
                        let res = await _this.r_insert_code_type_info(data);
                        if (res != null) {
                            _this.codeTypeData = res;
                            _this.isDisabled = true;
                            _this.$message.success('新增代码表类型信息成功');
                        } else {
                            _this.$message.error('新增代码表类型信息失败');
                        }
                    }
                })
            },
            updateCodeTypeInfo()/*更新代码表类型信息*/ {
                let _this = this;
                this.$refs['pageDataRef'].validate(async (valid) => {
                    if (valid) {
                        let data = _this.pageData;
                        let res = await _this.r_update_code_type_info(data);
                        if (res != null) {
                            _this.codeTypeData = res;
                            _this.isDisabled = true;
                            _this.$message.success('更新代码表类型信息成功');
                        } else {
                            _this.$message.error('更新代码表类型信息失败');
                        }
                    }
                })
            },
            addCodeInfoData()/*新增代码值信息*/ {
                let _this = this;
                this.$refs['codeDataRef'].validate(async (valid) => {
                    if (valid) {
                        let data = {
                            code: _this.pageData.code,
                            codeName: _this.pageData.codeName,
                            codeId: _this.codeData.codeId,
                            codeValue: _this.codeData.codeValue,
                            fatherCode: _this.codeData.fatherCode,
                            isCodeType: 0
                        };
                        let res = await _this.r_insert_code_data(data);
                        if (res != null) {
                            _this.$message.success('新增代码表值信息成功');
                            this.$refs['codeDataRef'].resetFields();
                            _this.codeInfoDataList.unshift(res);
                        } else {
                            _this.$message.error('更新代码表类型信息失败');
                        }
                    }
                })
            },
            deleteCodeInfoData(index, rows)/*删除代码值信息*/ {
                let data = rows[index];
                let res = this.r_delete_code_data(data)
                if (res != null) {
                    rows.splice(index, 1);
                    this.$message.success('删除代码表值信息成功');
                } else {
                    this.$message.success('删除代码表值信息失败');
                }
            },
            reset() /*重置代码表类型信息*/ {
                this.$refs['pageDataRef'].resetFields();
                this.$refs['codeDataRef'].resetFields();
            },
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>


