<template>
    <div  class="v-container">
        <!-- 面包屑 -->
        <el-breadcrumb>
            <el-breadcrumb-item :to="{ name: 'setting' }">设置</el-breadcrumb-item>
            <el-breadcrumb-item>代码表设置</el-breadcrumb-item>
        </el-breadcrumb>
        <hr>
        <!-- 列表 -->
        <list></list>
        <!-- 默认隐藏的详情页【对话框形式】-->
        <info></info>

    </div>
</template>

<script>
    import list from './codeManage/list.vue'
    import info from './codeManage/info.vue'
    export default {
        name: 'codeManage',
        components: {list, info},
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



